.TodoSearch {
  background: #F9FBFC;
  border-radius: 2px;
  border: 2px solid #202329;
  box-sizing: border-box;
  padding-left: 12px;
  margin: 0 auto;
  height: 64px;
  width: calc(100% - 62px);
  max-width: 500px;
  min-width: 280px;
  font-size: 2.4rem;
  text-align: left;
  font-family: "Montserrat";
  font-weight: 400;
  color: #1E1E1F;
  box-shadow: 0px 5px 50px rgba(32, 35, 41, 0.25);
}

.TodoSearch::placeholder {
  margin-left: 20px;
  color: #A5A5A5;
  font-family: "Montserrat";
  font-weight: 400;
}

.TodoSearch:focus{
  outline-color: #61DAFA;
}

.warning {
  font-size: 1.6rem;
}